.main {
  position: relative;
  min-height: 100vh;
}
.option {
  color: black;
  margin-left: 30px;
}
.main > h1 {
  margin: 20px 0 20px 30px;
}
.main_filter {
  margin: 10px 0 10px 0px;
  padding: 10px 0;
  background-color: rgb(231, 228, 228);
  display: flex;
  flex-direction: column;
}

.main_filter > form {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  margin: 10px 0px 10px 30px;
}
.main_filter > form > label > span {
  margin-bottom: 5px;
  font-weight: bold;
}
.main_filter > form > label > select,
.main_filter > form > label > input {
  padding: 5px 0;
  border-radius: 5px;
  text-indent: 5px;
}
.main_filter > form > label {
  display: flex;
  flex-direction: column;
  width: 400px;
}
.main_pagination {
  margin: 20px 0;
  display: flex;
  justify-content: center;
  font-size: 16px;
}
.main_pagination_page {
  height: 30px;
  width: 30px;
}
.main_pagination_page {
  display: none;
  border-radius: 50%;
  border: 1px solid black;
  background-color: transparent;
  margin-right: 5px;
  cursor: pointer;
}
.main_pagination_page:nth-child(-n + 1),
.main_pagination_page:nth-last-child(-n + 1) {
  border-radius: 50%;
  border: 1px solid black;
  background-color: transparent;
  margin-right: 5px;
  cursor: pointer;
  display: block;
}
.main_pagination_button_active {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 1px solid black;
  background-color: rgb(183, 225, 238);
  margin-right: 5px;
  cursor: pointer;
}
